// Gvenli kullanc kimlik dorulama Context'i
import React, { createContext, useContext, useState, useEffect } from 'react';
import { onAuthStateChanged, signInWithPopup, signOut } from 'firebase/auth';
import { auth, googleProvider } from '../firebase';

// Gvenli domain listesi (environment'dan alnabilir)
const ALLOWED_EMAIL_DOMAINS = process.env.REACT_APP_ALLOWED_DOMAINS 
  ? process.env.REACT_APP_ALLOWED_DOMAINS.split(',')
  : ['gmail.com', 'hotmail.com', 'outlook.com', 'yahoo.com']; // Fallback domains

// Email domain kontrol
const isEmailDomainAllowed = (email) => {
  if (!email) return false;
  const domain = email.split('@')[1]?.toLowerCase();
  return ALLOWED_EMAIL_DOMAINS.some(allowedDomain => 
    domain === allowedDomain.toLowerCase().trim()
  );
};

// Context olutur
const AuthContext = createContext();

// Context'i kullanmak iin custom hook
export function useAuth() {
  return useContext(AuthContext);
}

// Gvenli AuthProvider component'i
export function AuthProvider({ children }) {
  const [currentUser, setCurrentUser] = useState(null);
  const [loading, setLoading] = useState(true);
  const [authError, setAuthError] = useState(null);

  // Gvenli Google ile giri yapma fonksiyonu
  const signInWithGoogle = async () => {
    try {
      setAuthError(null);
      
      // Popup ile giri yap
      const result = await signInWithPopup(auth, googleProvider);
      const user = result.user;

      // Email domain kontrol
      if (!isEmailDomainAllowed(user.email)) {
        await signOut(auth); // Kullancy kart
        throw new Error(`Bu email domain'i (${user.email.split('@')[1]}) ile giri yaplamaz. zin verilen domain'ler: ${ALLOWED_EMAIL_DOMAINS.join(', ')}`);
      }
      
      // Email dorulama kontrol (opsiyonel uyar)
      if (!user.emailVerified) {
        console.warn('Email dorulanmam kullanc girii:', user.email);
        // Burada kullancya email dorulama mesaj gsterilebilir
      }
      
      // Baarl giri loglar
      console.log('Gvenli giri baarl:', {
        email: user.email,
        uid: user.uid,
        emailVerified: user.emailVerified,
        domain: user.email.split('@')[1]
      });
      
      return result;
      
    } catch (error) {
      console.error('Gvenli giri hatas:', error);
      setAuthError(error.message);
      throw error;
    }
  };

  // Gvenli k yapma fonksiyonu
  const logout = async () => {
    try {
      setAuthError(null);
      
      // Kullanc session temizleme
      if (currentUser) {
        console.log('Kullanc k yapyor:', currentUser.email);
        
        // Local storage temizleme (rate limiting verilerini koru)
        const rateLimit = {};
        for (let i = 0; i < localStorage.length; i++) {
          const key = localStorage.key(i);
          if (key && key.startsWith('rate_limit_')) {
            rateLimit[key] = localStorage.getItem(key);
          }
        }
        
        localStorage.clear();
        
        // Rate limit verilerini geri ykle
        Object.keys(rateLimit).forEach(key => {
          localStorage.setItem(key, rateLimit[key]);
        });
      }
      
      return await signOut(auth);
      
    } catch (error) {
      console.error('k hatas:', error);
      setAuthError(error.message);
      throw error;
    }
  };

  // Gvenli kullanc durumu dinleyicisi
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      try {
        if (user) {
          // Email domain kontrol (her auth state deiiminde)
          if (!isEmailDomainAllowed(user.email)) {
            console.warn('zin verilmeyen domain ile giri tespit edildi:', user.email);
            await signOut(auth);
            setAuthError('Bu email domain\'i ile giri yaplamaz');
            setCurrentUser(null);
            setLoading(false);
            return;
          }
          
          // Gvenlik loglar
          console.log('Auth state deiti - kullanc giri yapt:', {
            email: user.email,
            uid: user.uid,
            emailVerified: user.emailVerified
          });
        } else {
          console.log('Auth state deiti - kullanc k yapt');
        }
        
      setCurrentUser(user);
      setLoading(false);
        
      } catch (error) {
        console.error('Auth state deiiklik hatas:', error);
        setAuthError(error.message);
        setCurrentUser(null);
        setLoading(false);
      }
    });

    // Cleanup function
    return unsubscribe;
  }, []);

  // Gvenli Context value
  const value = {
    currentUser,
    signInWithGoogle,
    logout,
    loading,
    authError,
    isEmailDomainAllowed // Domain kontrol iin utility
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
} 