// rn dzenleme modal component'i
import React, { useState, useEffect } from 'react';
import { ref, update, remove } from 'firebase/database';
import { db } from '../firebase';
import { validateInput, rateLimiter, checkFirebaseAuth } from '../utils/security';
import { useAuth } from '../contexts/AuthContext';
import './ProductEditModal.css';

function ProductEditModal({ product, onClose, onProductUpdated }) {
  const { currentUser } = useAuth();
  const [formData, setFormData] = useState({
    name: '',
    brand: '',
    category: 'yatak',
    description: ''
  });
  const [variants, setVariants] = useState([]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  // Kategoriler listesi
  const categories = ['yatak', 'kanepe', 'koltuk', 'masa', 'sandalye', 'dolap', 'dier'];

  // Modal aldnda rn verilerini ykle
  useEffect(() => {
    if (product) {
      setFormData({
        name: product.name,
        brand: product.brand || '',
        category: product.category,
        description: product.description || ''
      });

      // Varyantlar ykle
      if (product.variants && product.variants.length > 0) {
        setVariants([...product.variants]);
      } else {
        // Eski sistem iin tek varyant olutur
        setVariants([{
          colorCode: '',
          colorName: 'Standart',
          quantity: product.quantity || 0
        }]);
      }
    }
  }, [product]);

  // Form input deiikliklerini yakala
  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  // Varyant ilemleri
  const handleVariantChange = (index, field, value) => {
    setVariants(prev => prev.map((variant, i) => 
      i === index ? { ...variant, [field]: value } : variant
    ));
  };

  const addVariant = () => {
    setVariants(prev => [...prev, { colorCode: '', colorName: '', quantity: 0 }]);
  };

  const removeVariant = (index) => {
    if (variants.length > 1) {
      setVariants(prev => prev.filter((_, i) => i !== index));
    }
  };

  // Toplam adet hesaplama
  const getTotalQuantity = () => {
    return variants.reduce((total, variant) => {
      return total + (parseInt(variant.quantity) || 0);
    }, 0);
  };

  // Gvenli form dorulama
  const validateForm = () => {
    try {
      // Kullanc authentication kontrol
      checkFirebaseAuth(currentUser);
      
      // Rate limiting kontrol (edit iin daha az kstlayc)
      rateLimiter.check('edit_product', 10, 60000); // 10 edit/dakika
      
      // Input sanitization ve validation
      const sanitizedName = validateInput.productName(formData.name);
      const sanitizedBrand = validateInput.brandName(formData.brand);
      const sanitizedDescription = validateInput.description(formData.description);
      
      if (!formData.category) {
        setError('Kategori seimi zorunludur');
        return false;
      }
      
      // Varyant dorulama
      for (let i = 0; i < variants.length; i++) {
        const variant = variants[i];
        
        try {
          validateInput.colorCode(variant.colorCode);
          validateInput.colorName(variant.colorName);
          validateInput.quantity(variant.quantity);
        } catch (validationError) {
          setError(`${i + 1}. renk: ${validationError.message}`);
          return false;
        }
      }
      
      return {
        name: sanitizedName,
        brand: sanitizedBrand,
        description: sanitizedDescription
      };
      
    } catch (securityError) {
      setError(securityError.message);
      return false;
    }
  };

  // Gvenli form gnderme
  const handleSubmit = async (e) => {
    e.preventDefault();
    setError('');

    const validationResult = validateForm();
    if (!validationResult) {
      return;
    }

    try {
      setLoading(true);
      
      // Ownership kontrol (rnn sahibi mi?)
      if (product.createdBy && product.createdBy !== currentUser.uid) {
        throw new Error('Bu rn dzenleme yetkiniz yok');
      }
      
      const productRef = ref(db, `products/${product.id}`);
      
      // Gvenli varyant sanitization
      const cleanVariants = variants.map((variant, index) => {
        try {
          return {
            colorCode: validateInput.colorCode(variant.colorCode),
            colorName: validateInput.colorName(variant.colorName),
            quantity: validateInput.quantity(variant.quantity)
          };
        } catch (err) {
          throw new Error(`Varyant ${index + 1}: ${err.message}`);
        }
      });
      
      // Gvenli gncelleme verisi
      const updateData = {
        name: validationResult.name,
        brand: validationResult.brand,
        category: formData.category,
        description: validationResult.description,
        variants: cleanVariants,
        totalQuantity: getTotalQuantity(),
        lastUpdatedAt: new Date().toISOString(),
        lastUpdatedBy: currentUser.uid,
        lastUpdatedByEmail: currentUser.email
      };
      
      // Eski sistemi temizle
      updateData.quantity = null;
      
      await update(productRef, updateData);
      
      onProductUpdated(); // rn listesini yenile
      onClose(); // Modal' kapat
      
    } catch (error) {
      console.error('rn gncellenirken hata olutu:', error);
      if (error.message.includes('yetkiniz') || error.message.includes('Varyant')) {
        setError(error.message);
      } else {
        setError('rn gncellenirken bir hata olutu. Ltfen tekrar deneyin.');
      }
    } finally {
      setLoading(false);
    }
  };

  // Gvenli rn silme ilemi
  const handleDeleteProduct = async () => {
    try {
      // Kullanc authentication kontrol
      checkFirebaseAuth(currentUser);
      
      // Rate limiting kontrol (silme ilemi iin daha sk)
      rateLimiter.check('delete_product', 3, 60000); // 3 silme/dakika
      
      // Ownership kontrol (rnn sahibi mi?)
      if (product.createdBy && product.createdBy !== currentUser.uid) {
        setError('Bu rn silme yetkiniz yok');
        return;
      }
      
      const confirmMessage = `"${product.name}" rnn silmek istediinizden emin misiniz?\n\nBu ilem geri alnamaz!`;
      
      if (!window.confirm(confirmMessage)) {
        return;
      }

      setLoading(true);
      const productRef = ref(db, `products/${product.id}`);
      await remove(productRef);
      
      onProductUpdated(); // rn listesini yenile
      onClose(); // Modal' kapat
      
    } catch (error) {
      console.error('rn silinirken hata olutu:', error);
      if (error.message.includes('yetkiniz') || error.message.includes('fazla deneme')) {
        setError(error.message);
      } else {
        setError('rn silinirken bir hata olutu. Ltfen tekrar deneyin.');
      }
    } finally {
      setLoading(false);
    }
  };

  // Modal dna tklama ile kapatma
  const handleBackdropClick = (e) => {
    if (e.target === e.currentTarget) {
      onClose();
    }
  };

  if (!product) return null;

  return (
    <div className="edit-modal-backdrop" onClick={handleBackdropClick}>
      <div className="edit-modal-content">
        <div className="edit-modal-header">
          <h2> rn Dzenle</h2>
          <button 
            onClick={onClose} 
            className="edit-close-btn"
            disabled={loading}
          >
            
          </button>
        </div>

        {error && <div className="edit-error-message">{error}</div>}

        <form onSubmit={handleSubmit} className="edit-product-form">
          <div className="edit-form-row">
            <div className="edit-form-group">
              <label htmlFor="edit-name">rn Ad *</label>
              <input
                type="text"
                id="edit-name"
                name="name"
                value={formData.name}
                onChange={handleInputChange}
                placeholder="rn ad"
                disabled={loading}
                required
              />
            </div>

            <div className="edit-form-group">
              <label htmlFor="edit-brand">Marka *</label>
              <input
                type="text"
                id="edit-brand"
                name="brand"
                value={formData.brand}
                onChange={handleInputChange}
                placeholder="rn: IKEA, Dota, Kelebek"
                disabled={loading}
                required
              />
            </div>
          </div>

          <div className="edit-form-row">
            <div className="edit-form-group">
              <label htmlFor="edit-category">Kategori *</label>
              <select
                id="edit-category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                disabled={loading}
                required
              >
                {categories.map(category => (
                  <option key={category} value={category}>
                    {category.charAt(0).toUpperCase() + category.slice(1)}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="edit-form-group">
            <label htmlFor="edit-description">Aklama</label>
            <textarea
              id="edit-description"
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              placeholder="rn aklamas (istee bal)"
              rows="3"
              disabled={loading}
            />
          </div>

          {/* Renk eitleri */}
          <div className="edit-variants-section">
            <div className="edit-variants-header">
              <label>Renk eitleri *</label>
              <span className="edit-total-quantity">Toplam: {getTotalQuantity()} adet</span>
            </div>
            
            <div className="edit-variants-list">
              {variants.map((variant, index) => (
                <div key={index} className="edit-variant-row">
                  <div className="edit-variant-inputs">
                    <input
                      type="text"
                      placeholder="Renk kodu (rn: 0046)"
                      value={variant.colorCode}
                      onChange={(e) => handleVariantChange(index, 'colorCode', e.target.value)}
                      disabled={loading}
                      className="edit-color-code-input"
                    />
                    <input
                      type="text"
                      placeholder="Renk ad (rn: Gri)"
                      value={variant.colorName}
                      onChange={(e) => handleVariantChange(index, 'colorName', e.target.value)}
                      disabled={loading}
                      className="edit-color-name-input"
                    />
                    <div className="edit-quantity-container">
                      <button
                        type="button"
                        onClick={() => handleVariantChange(index, 'quantity', Math.max(0, (parseInt(variant.quantity) || 0) - 1))}
                        disabled={loading || (parseInt(variant.quantity) || 0) === 0}
                        className="edit-quantity-btn minus"
                        title="Azalt (-1)"
                      >
                        
                      </button>
                      <input
                        type="number"
                        placeholder="Adet"
                        value={variant.quantity}
                        onChange={(e) => handleVariantChange(index, 'quantity', e.target.value)}
                        min="0"
                        disabled={loading}
                        className="edit-quantity-input"
                      />
                      <button
                        type="button"
                        onClick={() => handleVariantChange(index, 'quantity', (parseInt(variant.quantity) || 0) + 1)}
                        disabled={loading}
                        className="edit-quantity-btn plus"
                        title="Artr (+1)"
                      >
                        +
                      </button>
                    </div>
                  </div>
                  <button
                    type="button"
                    onClick={() => removeVariant(index)}
                    disabled={loading || variants.length === 1}
                    className="edit-remove-variant-btn"
                    title="Bu rengi sil"
                  >
                    Sil
                  </button>
                </div>
              ))}
            </div>
            
            <button
              type="button"
              onClick={addVariant}
              disabled={loading}
              className="edit-add-variant-btn"
            >
              + Yeni Renk Ekle
            </button>
          </div>

          <div className="edit-form-actions">
            <button 
              type="button" 
              onClick={handleDeleteProduct}
              className="edit-delete-btn"
              disabled={loading}
            >
              {loading ? 'Siliniyor...' : 'rn Sil'}
            </button>
            
            <div className="edit-action-buttons">
              <button 
                type="button" 
                onClick={onClose}
                className="edit-cancel-btn"
                disabled={loading}
              >
                ptal
              </button>
              <button 
                type="submit" 
                className="edit-submit-btn"
                disabled={loading}
              >
                {loading ? 'Gncelleniyor...' : 'Kaydet'}
              </button>
            </div>
          </div>
        </form>
      </div>
    </div>
  );
}

export default ProductEditModal; 