// Firebase konfigrasyon dosyas
import { initializeApp } from 'firebase/app';
import { getAuth, GoogleAuthProvider } from 'firebase/auth';
import { getDatabase } from 'firebase/database';
import { getAnalytics } from 'firebase/analytics';
import productionFirebaseConfig from './firebase-config-production';

// Firebase projesi ayarlar - Akll Config Selection
let firebaseConfig;

// Environment variables mevcut mu kontrol et
const hasEnvVars = process.env.REACT_APP_FIREBASE_API_KEY;

if (hasEnvVars) {
  // Gelitirme Modu: Environment Variables (Gvenli)
  firebaseConfig = {
  apiKey: process.env.REACT_APP_FIREBASE_API_KEY,
  authDomain: process.env.REACT_APP_FIREBASE_AUTH_DOMAIN,
  projectId: process.env.REACT_APP_FIREBASE_PROJECT_ID,
  storageBucket: process.env.REACT_APP_FIREBASE_STORAGE_BUCKET,
  messagingSenderId: process.env.REACT_APP_FIREBASE_MESSAGING_SENDER_ID,
  appId: process.env.REACT_APP_FIREBASE_APP_ID,
  measurementId: process.env.REACT_APP_FIREBASE_MEASUREMENT_ID
};
  console.log(' Gelitirme Modu: Environment variables kullanlyor');
} else {
  // Production Modu: Hardcoded Config (GitHub Pages iin)
  firebaseConfig = productionFirebaseConfig;
  console.log(' Production Modu: Hardcoded config kullanlyor');
}

// Konfigrasyon dorulama
if (!firebaseConfig.apiKey) {
  console.error(' Firebase konfigrasyonu eksik!');
  throw new Error('Firebase konfigrasyonu bulunamad');
}

console.log(' Firebase baaryla yaplandrld');

// Firebase uygulamasn balat
const app = initializeApp(firebaseConfig);

// Firebase servislerini export et
export const auth = getAuth(app);
export const db = getDatabase(app); // Realtime Database
export const analytics = getAnalytics(app);
export const googleProvider = new GoogleAuthProvider();

export default app; 