// Sadece giri yapm kullanclarn grebilecei sayfalar koruma sistemi
import React from 'react';
import { useAuth } from '../contexts/AuthContext';
import Login from './Login';

function ProtectedRoute({ children }) {
  const { currentUser } = useAuth();

  // Eer kullanc giri yapmamsa Login ekrann gster
  if (!currentUser) {
    return <Login />;
  }

  // Kullanc giri yapmsa, istenen sayfay gster
  return children;
}

export default ProtectedRoute; 